<template>
    <div id="cart">
       <navbar class="homeNav">
           <div slot="content">购物车({{cartshoplist | length}})</div>
       </navbar>
       <div class="cartshow" v-show="iscart">还没有商品添加进购物车</div>
       <scroll class="content" ref="scroll">
          <cartlist v-for="(item,index) in cartl" :key="index" :info="item"/>
       </scroll>
       <cartbar/>
    </div>
</template>
<script>
  import navbar from "@/components/navbar/navbar"
  import scroll from '@/components/bscroll/scroll'
  import cartlist from './cartlist'
  import cartbar from './cartbar'
 export default{
    name:'cart',
    data(){
      return{
        cartl:{},
        iscart:true
      }
    },
    computed:{
      cartshoplist(){
        return this.$store.state.cartList.length;
      }
    },
    activated(){
      this.cartl = this.$store.state.cartList;
      this.$refs.scroll.refresh();
    },
    watch:{
      cartl(){
        this.cartl.length >=1 ? this.iscart = false :this.iscart = true
      }
    },
    filters:{
       length(val){
          return val > 99? '99+':val;
       }
    },
    components:{
      navbar,
      scroll,
      cartlist,
      cartbar
    }
 }
</script>
<style scoped>
   #cart{
    height: 100vh;
   }
  .homeNav{
        background-color: #ff8198;
        color: #fff;
    }
    .content{
      height: calc(100% - 133px);
      overflow: hidden;
    }
   .cartshow{
    position: absolute;
    top: 49%;
    font-size: 16px;
    z-index: 12;
    width: 100%;
    height: 35px;
    text-align: center;
   }
</style>