<template>
	<div class="bottom-bar">
		<div class="check-content">
			<checkbutton class="check-button" :ischeck="isselectall" @click.native="checkclick" />
			<span>全选</span>
		</div>
		<div class="price">
           合计：{{totalPrice}}
		</div>
		<div class="calculate">
			去计算{{checklength}}
		</div>
	</div>
</template>
<script>
	import checkbutton from './checkbutton'
	export default{
		name:"cartbar",
		methods:{
           checkclick(){
             if(this.isselectall){//全部选中
               this.$store.state.cartList.forEach(item => item.checked = false)
             }else{//全部不选
               this.$store.state.cartList.forEach(item => item.checked = true)
             }
           }
		},
		computed:{
			totalPrice(){
				//计算价格,toFixed保留两位小数
				return '￥' + this.$store.state.cartList.filter(item =>{
					return item.checked
				}).reduce((preValue,item)=>{
                    return preValue + item.price * item.count
				},0).toFixed(2)
			},
			checklength(){
				return this.$store.state.cartList.filter(item =>item.checked).length
			},
			isselectall(){
				//全选状态显示
				if(this.$store.state.cartList.length === 0) return false
				return !this.$store.state.cartList.find(item => !item.checked)
			}
		},
		components:{
           checkbutton
		}
	}
</script>
<style scoped>
	.bottom-bar{
	position: relative;
	display: flex;
	height: 40px;
	line-height: 40px;
	background-color: #eee;
	}
	.check-content{
	width: 80px;
	font-size: 14px;
	display: flex;
	align-items: center;
	}
	.check-button{
    margin-left: 5px;
    margin-right: 5px;
	width: 20px;
	height: 20px;
	line-height: 20px;
	}
	.price{
		font-size: 14px;
		margin-left: 15px;
		flex: 1;
	}
	.calculate{
		text-align: center;
		font-size: 16px;
		color: #fff;
		background-color: orange;
		width: 90px;
	}
</style>