<template>
	<div class="check">
		<div class="radius" :class="{checked:ischeck}"></div>
	</div>
</template>
<script>
	export default{
		name:"check",
		props:{
			ischeck:{
				type:Boolean,
				default: false
			}
		}
	}
</script>
<style scoped>
	.radius{
		width: 15px;
		height: 15px;
		border-radius: 50%;
		border: 1px solid #ccc;
	}
	.checked{
		background-color: #ff5777;
		border:1px solid #ff5777;
	}
</style>